//Import dependencies
import React, {Fragment, useEffect, useState} from 'react';
import {connect} from 'react-redux';
import axios from 'axios';
import 'bulma';
import './index.scss';

//Import components
import Navbar from '../Navbar';
import Carousel from './Carousel';
import CourseCard from './CourseCard';

//Import Util
import util from '../../util';

//Map State to Props
const mapStateToProps = (state) => {
    return {}
}

//Build container
const TeacherView = (props) => {
    const id = props.match.params.id;
    const [teacher,
        setTeacher] = useState({
        name: {
            first: '',
            last: ''
        },
        social: {
            facebook: '',
            linkedin: '',
            twitter: ''
        },
        courses: [],
        avatar: {
            bucket: '',
            key: '',
            url: ''
        }
    });

    useEffect(() => {
        const main = async() => {
            try {
                let res = await axios.get(`/api/teacher/${id}`);
                let data = res.data.payload;
                setTeacher(data);   
            } catch (err) {
                err.response ? util.notify(props.dispatch, err.response.data.msg, "is-danger") : console.log(err);
            }
        }
        main();
    }, [id, props])

    return (
        <Fragment>
            <Navbar/>
            <section className="teacher-view">
                <div className="container">
                    <div className="has-text-centered is-flex-desktop" style={{"align-items" : "center"}}>
                        <figure className="image">
                            <img className="is-rounded display-pic" src={teacher.avatar.url} alt='dp' />
                        </figure>
                        <div className="intro">
                            <p className="is-size-2">{teacher.name.last ? teacher.name.first + " " + teacher.name.last : teacher.name.first}</p>
                            {/*<div className="social is-flex">
                                <FontAwesomeIcon
                                    icon={faFacebook}
                                    size="2x"
                                    style={{ color: '#4267B2' }}
                                />
                                <FontAwesomeIcon
                                    icon={faTwitter}
                                    size="2x"
                                    style={{ color: '#1DA1F2' }}
                                />
                                <FontAwesomeIcon
                                    icon={faLinkedin}
                                    size="2x"
                                    style={{ color: '#2867B2' }}
                                />
                            </div>*/}
                            <div className="stats is-flex has-text-centered">
                                <div className="stat has-text-centered">
                                    <p className="is-size-7">Courses</p>
                                    <p className="is-size-5">{teacher.courses.length}</p>
                                </div>
                                <div className="stat has-text-centered">
                                    <p className="is-size-7">Students</p>
                                    <p className="is-size-5">1509</p>
                                </div>
                                <div className="stat has-text-centered">
                                    <p className="is-size-7">Reviews</p>
                                    <p className="is-size-5">856</p>
                                </div>
                            </div>
                        </div>
                        <a href={`/teachers/${id}/edit`} style={{"margin-left" : "auto"}}><button className="button is-info">Edit</button></a>
                    </div>
                    <div className="about">
                        <p className="is-size-6"><strong>About Me</strong></p>
                        <p className="is-size-6">{teacher.about}</p>
                    </div>
                    <div className="courses">
                        <p className="is-size-6"><strong>{`My Courses (${teacher.courses.length})`}</strong></p>
                        <div className="cards is-flex-tablet">
                            <Carousel
                                cardsArr={
                                    teacher.courses.map(course =>
                                        <CourseCard
                                            courseImage="https://bulma.io/images/placeholders/1280x960.png" 
                                            courseName={course.title}
                                            teacherName={teacher.name.last ? teacher.name.first + " " + teacher.name.last : teacher.name.first}
                                            status={course.status}
                                            sp={150}
                                            mrp={1000}
                                        />
                                    )
                                }
                            />
                        </div>
                    </div>
                </div>
            </section>
        </Fragment>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(TeacherView);