//Import dependencies
import React, { useState } from 'react'
import { connect } from 'react-redux';
import {useLocation, useHistory} from 'react-router-dom';
import 'bulma';
import './index.scss'

//Import components

//Import actions & auth
import auth from '../../auth';

const mapStateToProps = (state) => {
    return {}
}

//Build container
const Login = (props) => {

    const [loginError, setLoginError] = useState('');
    let history = useHistory();
    let location = useLocation();
    let { from } = location.state || { from: { pathname: "/" } };

    const handleSubmit = async (event) => {
        event.preventDefault();
        const email = event.target.elements.email.value;
        const password = event.target.elements.password.value;
        const loginStatus = await auth.login(email, password, props.dispatch);
        history.replace(from);

        if (loginStatus[0] === "failed") {
            setLoginError(loginStatus[1])
        }
    }

    return (
        <section className="hero is-fullheight login">
            <div className="hero-body">
                <div className="container">
                    <div className="columns is-centered">
                        <div className="column is-5-tablet is-4-desktop is-3-widescreen">
                            <form action="" onSubmit={handleSubmit} className="box has-text-centered">
                                <p className="is-size-4 title">Welcome to Carlos!</p>
                                <div className="field has-text-centered">
                                    <div className="control">
                                        <input type="email" name="email" placeholder="Email" className="input" required />
                                    </div>
                                </div>
                                <div className="field has-text-centered">
                                    <div className="control">
                                        <input type="password" name="password" placeholder="Password" className="input" required />
                                    </div>
                                </div>
                                <div className="field has-text-centered">
                                    <p className="is-size-7">{loginError}</p>
                                </div>
                                <div className="field has-text-centered">
                                    <button type="submit" className="button is-success">Login</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(Login);