import axios from 'axios';
import {v4} from 'uuid';
import actions from '../state/actions';

export default async ( email, password, dispatch ) => {
        try {
            const res = await axios.post('/api/auth/local', { email, password });
            const data = await res.data;
            if (data.success) {
                const id = v4();
                dispatch(actions.logIn(data.payload.permission));
                dispatch(actions.setNotification(data.msg, id, "is-primary" ));
                setTimeout(() => {
                    dispatch(actions.removeNotification(id));
                }, 3000);
            }
            return [ "success", data ];
        } catch (err) {
            return [ "failed", err.response.data ];
        }
}