import React from 'react';

export default({
    activeTabId,
    course,
    courseTitle,
    setCourseTitle,
    courseSubTitle,
    setCourseSubTitle,
    courseMrp,
    setCourseMrp,
    courseSp,
    setCourseSp,
    handleOverviewSave,
    handleOverviewCancel,
    handleCourseLive,
    handleCoursePause
}) => (
    <div
        id="overview-view"
        className={`overview tab-view ${activeTabId === "overview" && "is-visible"}`}>
        <div className="status is-flex">
            <p className="has-text-weight-bold">Status</p>
            <p className="is-size-5">{course.status}</p>
            {(course.status === "INCOMPLETE" || course.status === "PAUSED")
                ? <button className="button is-success" onClick={handleCourseLive}>Make Live</button>
                : <button className="button is-warning" onClick={handleCoursePause}>Pause</button>}
        </div>
        <div className="other-details">
            <h1 className="is-size-4">Edit Details</h1>
            <form
                id="editCourseOverview"
                className="form"
                encType="multipart/form-data"
                onSubmit={handleOverviewSave}>
                <div className="item">
                    <label>Title</label>
                    <input
                        type="text"
                        className="input"
                        name="title"
                        placeholder="Course Title"
                        value={courseTitle}
                        onChange={(e) => {
                        e.preventDefault();
                        setCourseTitle(e.target.value);
                    }}
                        required/>
                </div>
                <div className="item">
                    <label>Subtitle</label>
                    <input
                        type="text"
                        className="input"
                        name="subtitle"
                        placeholder="Course Subtitle"
                        value={courseSubTitle}
                        onChange={(e) => {
                        e.preventDefault();
                        setCourseSubTitle(e.target.value);
                    }}
                        required/>
                </div>
                <div className="field is-horizontal">
                    <div className="item">
                        <label>Maximum Price</label>
                        <input
                            type="number"
                            className="input"
                            name="mrp"
                            placeholder={0}
                            value={courseMrp}
                            onChange={(e) => {
                            e.preventDefault();
                            setCourseMrp(parseInt(e.target.value));
                        }}
                            required/>
                    </div>
                    <div className="item">
                        <label>Selling Price</label>
                        <input
                            type="number"
                            className={`input ${courseSp > courseMrp
                            ? "is-danger"
                            : ""}`}
                            name="sp"
                            placeholder={0}
                            value={courseSp}
                            onChange={(e) => {
                            e.preventDefault();
                            setCourseSp(parseInt(e.target.value));
                        }}
                            required/>
                        <p className="is-size-7 error">{courseSp > courseMrp
                                ? "Selling price cannot be higher than Maximum Price"
                                : ""}</p>
                    </div>
                </div>
                <div className="buttons">
                    <button
                        className="button is-success"
                        type="submit"
                        disabled={courseTitle === course.title && courseSubTitle === course.subtitle && courseMrp === course.mrp && courseSp === course.sp}>
                        Save</button>
                    <button
                        className="button is-danger"
                        disabled={courseTitle === course.title && courseSubTitle === course.subtitle && courseMrp === course.mrp && courseSp === course.sp}
                        onClick={handleOverviewCancel}>
                        Cancel</button>
                </div>
            </form>
        </div>
    </div>
)