//Import dependencies
import React, {Fragment, useState, useEffect} from 'react';
import {connect} from 'react-redux';
import axios from 'axios';
import 'bulma';
import './index.scss';

//Import components
import Navbar from '../Navbar';

//Import utils
import util from '../../util';

//Map State to Props
const mapStateToProps = (state) => {
    return {}
}

//Build container
const Teachers = (props) => {
    const [teachers,
        setTeachers] = useState([]);
    const [filteredTeachers,
        setFilteredTeachers] = useState([]);
    const [searchName,
        setSearchName] = useState('');
    const [searchEmail,
        setSearchEmail] = useState('');
    const [modalOpen,
        setModalOpen] = useState(false);
    const [fileName,
        setFileName] = useState('No file chosen');

    useEffect(() => {
        const main = async() => {
            try {
                let response = await axios.get('api/teacher/all');
                setTeachers(response.data.payload);
                setFilteredTeachers(response.data.payload);
            } catch (err) {
                err.response ? util.notify(props.dispatch, err.response.data.msg, "is-danger") : console.log(err);
            }
        }

        main();
    }, [props])

    const onModalToggle = (e) => {
        e.preventDefault();
        setModalOpen(!modalOpen);
    }

    const onSearchNameChange = (e) => {
        e.preventDefault();
        setSearchName(e.target.value);
    }

    const onSearchEmailChange = (e) => {
        e.preventDefault();
        setSearchEmail(e.target.value);
    }

    const handleSubmit = async(event) => {
        event.preventDefault();
        let form = document.getElementById('createTeacher');
        let payload = new FormData(form);
        onModalToggle(event);

        try {
            const res = await axios.post("api/teacher", payload);
            setTeachers(res.data.payload);
            setFilteredTeachers(res.data.payload);
            util.notify(props.dispatch, "Teacher Created", "is-success");
        } catch (err) {
            err.response ? util.notify(props.dispatch, err.response.data.msg, "is-danger") : console.log(err);
        }
    }

    const onClickSearch = (e) => {
        e.preventDefault();
        const searchedTeachers = teachers.filter(teacher => {
            let teacherName = '';
            teacher.name.last
                ? (teacherName = teacher.name.first + ' ' + teacher.name.last)
                : (teacherName = teacher.name.first);
            let teacherEmail = teacher.email;
            if ((teacherName.toLowerCase().indexOf(searchName.toLowerCase()) !== -1) && (teacherEmail.toLowerCase().indexOf(searchEmail.toLowerCase()) !== -1)) {
                return true;
            } else {
                return false;
            }
        })
        setFilteredTeachers(searchedTeachers);
    }

    const onClickResetSearch = async(e) => {
        e.preventDefault();
        setSearchName('');
        setSearchEmail('');
        setFilteredTeachers(teachers);
    }

    const onClickDelete = async(e) => {
        e.preventDefault();
        const id = e.target.value;
        try {
            const res = await axios.delete(`/api/teacher/${id}`);
            setTeachers(res.data.payload);
            setFilteredTeachers(res.data.payload);
            util.notify(props.dispatch, "Teacher Deleted", "is-success");
        } catch (err) {
            util.notify(props.dispatch, err.response.data.msg, "is-danger");
        }
    }

    return (
        <Fragment>
            <Navbar/>
            <section className="teachers">
                <div className="container">
                    <h1 className="is-size-3">Teachers</h1>
                    <div className="actions is-flex">
                        <div className="search is-flex">
                            <input
                                className="input search-box"
                                type="text"
                                placeholder="Search by name"
                                onChange={onSearchNameChange}
                                value={searchName}/>
                            <input
                                className="input search-box"
                                type="text"
                                placeholder="Search by email"
                                onChange={onSearchEmailChange}
                                value={searchEmail}/>
                            <button className="button is-primary" onClick={onClickSearch}>Search</button>
                            <button className="button is-info" onClick={onClickResetSearch}>Reset</button>
                        </div>
                        <div className="add is-flex" onClick={onModalToggle}>
                            <p className="is-size-5">Add Teacher</p>
                            <img
                                src={process.env.PUBLIC_URL + '/images/add.png'}
                                alt="add"
                                className="icon"/>
                        </div>
                    </div>
                    <div className={`modal ${modalOpen && 'is-active'}`}>
                        <div className="modal-background"></div>
                        <div className="modal-card">
                            <header className="modal-card-head">
                                <p className="modal-card-title">New Teacher</p>
                                <button className="delete" aria-label="close" onClick={onModalToggle}></button>
                            </header>
                            <section className="modal-card-body">
                                <form
                                    id="createTeacher"
                                    onSubmit={handleSubmit}
                                    className="form"
                                    encType="multipart/form-data">
                                    <div className="field is-horizontal">
                                        <input
                                            type="text"
                                            className="input first"
                                            name="firstname"
                                            placeholder="First Name"
                                            required/>
                                        <input
                                            type="text"
                                            className="input"
                                            name="lastname"
                                            placeholder="Last Name"
                                            required/>
                                    </div>
                                    <input
                                        type="email"
                                        className="input item"
                                        name="email"
                                        placeholder="Email Id"
                                        required/>
                                    <textarea
                                        className="textarea item"
                                        name="about"
                                        placeholder="About Teacher. e.g. I am a professional poker player for 6years..."/>
                                    <input
                                        type="url"
                                        className="input item"
                                        name="facebook"
                                        placeholder="Facebook"/>
                                    <input
                                        type="url"
                                        className="input item"
                                        name="linkedin"
                                        placeholder="Linkedin"/>
                                    <input type="url" className="input item" name="twitter" placeholder="Twitter"/>
                                    <div className="file has-name is-fullwidth">
                                        <label className="file-label">
                                            <input
                                                className="file-input"
                                                type="file"
                                                name="avatar"
                                                onChange={(e) => {
                                                setFileName(e.target.files[0].name)
                                            }}/>
                                            <span className="file-cta">
                                                <span className="file-label">
                                                    Upload Profile Image
                                                </span>
                                            </span>
                                            <span className="file-name">{fileName}</span>
                                        </label>
                                    </div>
                                </form>
                            </section>
                            <footer className="modal-card-foot">
                                <button form="createTeacher" type="submit" className="button is-success">Add</button>
                                <button className="button" onClick={onModalToggle}>Cancel</button>
                            </footer>
                        </div>
                    </div>
                    <table className="table is-bordered is-hoverable is-fullwidth">
                        <thead>
                            <tr>
                                <th>Id</th>
                                <th>Name</th>
                                <th>Email Id</th>
                                <th>Courses</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {filteredTeachers.map(teacher => {
                                let teacherName = '';
                                teacher.name.last
                                    ? (teacherName = teacher.name.first + ' ' + teacher.name.last)
                                    : (teacherName = teacher.name.first);
                                return (
                                    <tr key={teacher._id}>
                                        <td>{teacher._id}</td>
                                        <td>{teacherName}</td>
                                        <td>{teacher.email}</td>
                                        <td>{teacher.courses.length}</td>
                                        <td>
                                            <a href={`/teachers/${teacher._id}/edit`}>
                                                <button className="button is-small is-info">Edit</button>
                                            </a>
                                            <a href={`/teachers/${teacher._id}/view`}>
                                                <button className="button is-small is-primary">View</button>
                                            </a>
                                            {teacher.courses.length === 0
                                                ? <button className="button is-small is-danger" value={teacher._id} onClick={onClickDelete} >Delete</button>
                                                : null}
                                        </td>
                                    </tr>
                                )
                            })}
                        </tbody>
                    </table>
                </div>
            </section>
        </Fragment>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(Teachers);