import React, { Fragment } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { connect } from 'react-redux';
import 'bulma';
import './index.scss';

import Notifications from '../Notifications';
import Home from '../Home';
import Teachers from '../Teachers';
import TeacherView from '../TeacherView';
import TeacherEdit from '../TeacherEdit';
import Courses from '../Courses';
import CourseEdit from '../CourseEdit';
import CourseView from '../CourseView';
import Users from '../Users';
import Login from '../Login'
import ProtectedRoute from '../ProtectedRoute';

const mapStateToProps = (state) => {
  return { }
}

const App = (props) => {
  return (
    <Fragment>
      <Notifications />
      <Router>
        <Switch>
          <Route exact path='/login' {...props} component={Login} />
          <ProtectedRoute exact path='/' {...props} component={Home} />
          <ProtectedRoute exact path='/teachers' {...props} component={Teachers} />
          <ProtectedRoute path='/teachers/:id/view' {...props} component={TeacherView} />
          <ProtectedRoute path='/teachers/:id/edit' {...props} component={TeacherEdit} />
          <ProtectedRoute exact path='/courses' {...props} component={Courses} />
          <ProtectedRoute path='/courses/:id/edit' {...props} component={CourseEdit} />
          <ProtectedRoute path='/courses/:id/view/:as' {...props} component={CourseView} />
          <ProtectedRoute exact path='/users' {...props} component={Users} />
        </Switch>
      </Router>
    </Fragment>
  )
}

export default connect(mapStateToProps)(App);
