import React from 'react';
import 'bulma';
import './index.scss';

export default ({ courseImage, courseName, teacherName, status, sp, mrp }) => (
    <div className="card course-card">
        <div className="card-image">
            <figure className="image is-4by3">
                <img src={courseImage} alt="Course" />
            </figure>
        </div>
        <div className="card-content">
            <div className="media">
                <div className="media-content">
                    {(status === 'INCOMPLETE' || status === 'PAUSED') ? <p className="is-size-7" style={{"color" : "red"}}>{status}</p> : null}
                    <p className="title is-5">{courseName}</p>
                    <p className="subtitle is-7">{teacherName}</p>
                    <div className="price is-flex">
                        <p className="is-size-5">{` ${sp}`}</p>
                        <p className="is-size-6 obsolete"><strike>{` ${mrp}`}</strike></p>
                    </div>
                </div>
            </div>
        </div>
    </div>
)