//Import dependencies
import React, {Fragment, useState, useEffect} from 'react';
import {connect} from 'react-redux';
import axios from 'axios';
import 'bulma';
import './index.scss';

//Import components
import Navbar from '../Navbar';
import Overview from './Overview';
import Description from './Description';
import Sections from './Sections';

//Import Util
import util from '../../util';

//Map State to Props
const mapStateToProps = (state) => {
    return {}
}

//Build container
const CourseEdit = (props) => {

    const id = props.match.params.id;

    const [course,
        setCourse] = useState({
        status: '',
        teacher: '',
        title: '',
        subtitle: '',
        description: '',
        mrp: 0,
        sp: 0,
        sections: []
    });

    const [courseDescription,
        setCourseDescription] = useState('');
    const [activeTabId,
        setActiveTabId] = useState('overview');
    const [courseTitle,
        setCourseTitle] = useState('');
    const [courseTitleId,
        setCourseTitleId] = useState('');
    const [courseSubTitle,
        setCourseSubTitle] = useState('');
    const [courseMrp,
        setCourseMrp] = useState(0);
    const [courseSp,
        setCourseSp] = useState(0);
    const [courseSections,
        setCourseSections] = useState([]);
    const [courseLessons,
        setCourseLessons] = useState([]);
    const [newSectionName,
        setNewSectionName] = useState('');
    const [activeSectionId,
        setActiveSectionId] = useState('');
    const [addLessonOpen,
        setAddLessonOpen] = useState(false);
    const [uploadStatus,
        setUploadStatus] = useState(0);

    const flattenCourse = (data) => ({
        status: data.status,
        teacher: (data.teacher.name.last
            ? data.teacher.name.first + " " + data.teacher.name.last
            : data.teacher.name.first),
        title: data.title,
        titleId: data.titleId,
        subtitle: data.subtitle || '',
        description: data.description || '',
        mrp: (data.fees
            ? data.fees.mrp || 0
            : 0),
        sp: (data.fees
            ? data.fees.sp || 0
            : 0),
        sections: data.sections,
        lessons: data.lessons
    })

    const updateFields = (flattenedCourse) => {
        setCourse(flattenedCourse);
        setCourseDescription(flattenedCourse.description);
        setCourseTitle(flattenedCourse.title);
        setCourseTitleId(flattenedCourse.titleId);
        setCourseSubTitle(flattenedCourse.subtitle);
        setCourseMrp(flattenedCourse.mrp);
        setCourseSp(flattenedCourse.sp);
        setCourseSections(flattenedCourse.sections);
        setCourseLessons(flattenedCourse.lessons);
        setUploadStatus(0);
        setAddLessonOpen(false);
        if (flattenedCourse.sections.length > 0 && activeSectionId === "") {
            setActiveSectionId(flattenedCourse.sections[0].id);
        }
    }

    useEffect(() => {
        const main = async() => {
            try {
                let res = await axios.get(`/api/course/${id}`);
                let data = res.data.payload;
                let flattenedCourse = flattenCourse(data);
                updateFields(flattenedCourse);

            } catch (err) {
                err.response
                    ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                    : console.log(err);
            }

        }
        main();
    }, [props, id])

    //handle tab change
    const onClickTab = (e) => {
        e.preventDefault();
        setActiveTabId(e.currentTarget.id);
    }

    //Handlers for Overview View
    const handleOverviewSave = async(e) => {
        e.preventDefault();
        let form = document.getElementById('editCourseOverview');
        let payload = new FormData(form);

        try {
            const res = await axios.put(`/api/course/${id}/overview`, payload);
            const data = res.data.payload;
            let flattenedCourse = flattenCourse(data);
            updateFields(flattenedCourse);
            util.notify(props.dispatch, "Course Overview Updated", "is-success");
        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }
    }
    const handleOverviewCancel = (e) => {
        e.preventDefault();
        updateFields(course);
    }

    const handleCourseLive = async(e) => {
        try {
            e.preventDefault();
            const res = await axios.put(`/api/course/${id}/status/live`);
            const data = res.data.payload;
            let flattenedCourse = flattenCourse(data);
            updateFields(flattenedCourse);
            util.notify(props.dispatch, "Course is now Live", "is-success");

        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }
    }

    const handleCoursePause = async(e) => {
        try {
            e.preventDefault();
            const res = await axios.put(`/api/course/${id}/status/pause`);
            const data = res.data.payload;
            let flattenedCourse = flattenCourse(data);
            updateFields(flattenedCourse);
            util.notify(props.dispatch, "Course is now Paused", "is-success");

        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }
    }

    //Handlers for Description View
    const handleEditorChange = (content, editor) => {
        setCourseDescription(content);
    }

    const handleEditorSave = async(e) => {
        e.preventDefault();
        try {
            let res = await axios.put(`/api/course/${id}/description`, {description: courseDescription});
            let data = res.data.payload;
            let flattenedCourse = flattenCourse(data);
            updateFields(flattenedCourse);
            util.notify(props.dispatch, "Course description updated", "is-success");
        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }
    }

    const handleEditorCancel = (e) => {
        e.preventDefault();
        updateFields(course);
    }

    //handlers for section view
    const handleNewSectionAdd = async(e) => {
        e.preventDefault();
        try {
            let res = await axios.post(`/api/course/${id}/section`, {newSectionName});
            let data = res.data.payload;
            let flattenedCourse = flattenCourse(data);
            updateFields(flattenedCourse);
            setNewSectionName('');
            util.notify(props.dispatch, "New Section added", "is-success");
        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }

    }

    const handleNewSectionCancel = (e) => {
        e.preventDefault();
        setNewSectionName('');
    }

    const handleSectionDelete = async(e) => {
        e.preventDefault();
        try {
            let res = await axios.delete(`/api/course/${id}/section/${activeSectionId}`);
            let data = res.data.payload;
            let flattenedCourse = flattenCourse(data);
            updateFields(flattenedCourse);
            util.notify(props.dispatch, "Section Deleted", "is-success");
        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }

    }

    const handleNewLessonAdd = async(e, setFileName) => {
        e.preventDefault();
        if (activeSectionId === "") {
            util.notify(props.dispatch, "Please select a section before adding a lesson", "is-danger");
            return;
        }
        try {
            let form = document.getElementById('addNewLesson');
            let formData = new FormData(form);
            const file = formData.get("resource");
            const title = formData.get('title');
            const lessontype = formData.get('lessontype');
            const lessonaccess = formData.get('lessonaccess')
            const putRes = await axios.get(`/api/lesson/puturl/${file.name}`);
            const puturl = putRes.data.payload.url;
            const bucket = putRes.data.payload.bucket;
            const key = putRes.data.payload.key;
            if (lessontype === "VIDEO" && file.type.indexOf("video") === -1) {
                util.notify(props.dispatch, "Expected video file, got something else.", "is-danger");
                return;
            } else if (lessontype === "DOC" && file.type.indexOf("pdf") === -1) {
                util.notify(props.dispatch, "Expected pdf file, got something else.", "is-danger");
                return;
            }
            util.notify(props.dispatch, "Upload will start in a few seconds. Do not refresh this page.", "is-success");
            const options = {
                headers: {
                    "Content-Type": file.type
                },
                onUploadProgress: (e) => {
                    var completed = Math.round(e.loaded * 100 / e.total);
                    setUploadStatus(completed);
                }
            }
            await axios.put(puturl, file, options);

            let payload = {
                title,
                lessontype,
                lessonaccess,
                bucket,
                key,
            }
            const res = await axios.post(`/api/course/${id}/section/${activeSectionId}/lesson`, payload);
            const data = res.data.payload;
            let flattenedCourse = flattenCourse(data);
            updateFields(flattenedCourse);
            form.reset();
            setFileName('');
            util.notify(props.dispatch, "Lesson Added", "is-success");
        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }
    }

    const handleLessonView = async(e) => {
        e.preventDefault();
        try {
            let res = await axios.get(`/api/lesson/${e.target.value}/url`);
            let data = res.data.payload;
            window.open(data);
        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }
    }

    const handleLessonDelete = async(e, lessonid) => {
        e.preventDefault();
        try {
            const res = await axios.delete(`/api/course/${id}/lesson/${lessonid}`);
            const data = res.data.payload;
            let flattenedCourse = flattenCourse(data);
            updateFields(flattenedCourse);
            util.notify(props.dispatch, "Lesson Deleted", "is-success");
        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }
    }

    return (
        <Fragment>
            <Navbar/>
            <section className="course-edit">
                <div className="container">
                    <div className="is-flex heading">
                        <div>
                            <h1 className="is-size-3">Edit Course</h1>
                            <p className="is-size-5">{`${course.title} by ${course.teacher}`}</p>
                        </div>
                        <div className="buttons">
                            <a href={`/courses/${courseTitleId}/view/preview`}>
                                <button className="button is-info">View Course</button>
                            </a>
                        </div>
                    </div>
                    <div className="tabs is-boxed">
                        <ul>
                            <li
                                id="overview"
                                className={activeTabId === "overview"
                                ? "is-active"
                                : null}
                                onClick={onClickTab}>
                                <a href="#overview">
                                    <span>Overview</span>
                                </a>
                            </li>
                            <li
                                id="description"
                                className={activeTabId === "description"
                                ? "is-active"
                                : null}
                                onClick={onClickTab}>
                                <a href="#description">
                                    <span>Description</span>
                                </a>
                            </li>
                            <li
                                id="sections"
                                className={activeTabId === "sections"
                                ? "is-active"
                                : null}
                                onClick={onClickTab}>
                                <a href="#sections">
                                    <span>Sections</span>
                                </a>
                            </li>
                        </ul>
                    </div>
                    <Overview
                        activeTabId={activeTabId}
                        course={course}
                        courseTitle={courseTitle}
                        setCourseTitle={setCourseTitle}
                        courseSubTitle={courseSubTitle}
                        setCourseSubTitle={setCourseSubTitle}
                        courseMrp={courseMrp}
                        setCourseMrp={setCourseMrp}
                        courseSp={courseSp}
                        setCourseSp={setCourseSp}
                        handleOverviewSave={handleOverviewSave}
                        handleOverviewCancel={handleOverviewCancel}
                        handleCourseLive={handleCourseLive}
                        handleCoursePause={handleCoursePause}/>
                    <Description
                        activeTabId={activeTabId}
                        course={course}
                        courseDescription={courseDescription}
                        handleEditorChange={handleEditorChange}
                        handleEditorCancel={handleEditorCancel}
                        handleEditorSave={handleEditorSave}/>
                    <Sections
                        activeTabId={activeTabId}
                        course={course}
                        courseSections={courseSections}
                        setCourseSections={setCourseSections}
                        courseLessons={courseLessons}
                        setCourseLessons={setCourseLessons}
                        newSectionName={newSectionName}
                        setNewSectionName={setNewSectionName}
                        handleNewSectionAdd={handleNewSectionAdd}
                        handleNewSectionCancel={handleNewSectionCancel}
                        handleSectionDelete={handleSectionDelete}
                        activeSectionId={activeSectionId}
                        setActiveSectionId={setActiveSectionId}
                        handleNewLessonAdd={handleNewLessonAdd}
                        addLessonOpen={addLessonOpen}
                        setAddLessonOpen={setAddLessonOpen}
                        handleLessonView={handleLessonView}
                        handleLessonDelete={handleLessonDelete}
                        uploadStatus={uploadStatus}
                        setUploadStatus={setUploadStatus}/>
                </div>
            </section>
        </Fragment>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(CourseEdit);