//Import dependencies
import React, {Fragment, useState, useEffect} from 'react';
import {connect} from 'react-redux';
import 'bulma';
import './index.scss';

//Import components
import Navbar from '../Navbar';
import Viewport from './Viewport';

//Import utils
import util from '../../util';
import axios from 'axios';

//Map State to Props
const mapStateToProps = (state) => {
    return {}
}

//Build container
const CourseView = (props) => {

    const {id, as} = props.match.params;

    const [course,
        setCourse] = useState({});
    const [activeLesson,
        setActiveLesson] = useState({});
    const [lessonUrl,
        setLessonUrl] = useState('');

    useEffect(() => {

        const main = async() => {
            try {
                let res = await axios.get(`/api/course/${as}/${id}`);
                const data = res.data.payload;
                setCourse(data);
                setActiveLesson(data.lessons[0]);
                let res2 = await axios.get(`/api/lesson/${data.lesson[0]}/url`);
                const url = res2.data.payload;
                setLessonUrl(url);
            } catch (err) {
                err.response
                    ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                    : console.log(err);
            }
        }

        main();
    }, [as, id, props.dispatch])

    const handleLessonClick = async(e, lesson) => {
        e.preventDefault();
        try {
            if(lesson.lesson === "no-access") {
                util.notify(props.dispatch, "Resource blocked by paywall or auth wall.", "is-success")
            } else {
                let res = await axios.get(`/api/lesson/${lesson.lesson}/url`);
                const url = res.data.payload;
                setActiveLesson(lesson);
                setLessonUrl(url);
            }
        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }
    }

    return (
        <Fragment>
            <div className="is-flex container view-toggler">
                <p>View:</p>
                <div className="is-flex view-tabs">
                    <a
                        href={`/courses/${id}/view/preview`}
                        className={`${as === "preview" && "is-active"}`}>Preview</a>
                    <a
                        href={`/courses/${id}/view/freeview`}
                        className={`${as === "freeview" && "is-active"}`}>Freeview</a>
                    <a
                        href={`/courses/${id}/view/premium`}
                        className={`${as === "premium" && "is-active"}`}>Premium</a>
                </div>
            </div>
            <Navbar/>
            <section className="course-view">
                <div className="course-title">
                    <p className="is-size-4">{course.title}</p>
                    <p className="is-size-6">{course.subtitle}</p>
                </div>
                <div className="container is-flex">
                    <div className="course-content">
                        <Viewport lesson={activeLesson} lessonUrl={lessonUrl}/>
                    </div>
                    <aside className="menu">
                        <p className="menu-label">
                            <strong>Course Content</strong>
                        </p>
                        <ul className="menu-list">
                            {course.sections
                                ? course
                                    .sections
                                    .map((sec, i) => {
                                        return (
                                            <li key={i}>
                                                <a href="#section">{`Section ${i + 1}: ${sec.title}`}</a>
                                                <ul className="lessons">
                                                    {course.lessons
                                                        ? course
                                                            .lessons
                                                            .filter(lesson => lesson.sectionid === sec.id)
                                                            .map((lesson, index) => (
                                                                <li key={index} onClick={(e) => handleLessonClick(e, lesson)}>
                                                                    <a href="#lesson">{`${index + 1}. ${lesson.title}`}</a>
                                                                </li>
                                                            ))
                                                        : ""
}
                                                </ul>
                                            </li>
                                        )
                                    })
                                : ""}
                        </ul>
                    </aside>
                </div>
            </section>
        </Fragment>
    )
}

// Connect with redux and export
export default connect(mapStateToProps)(CourseView);