import React, {useState, useEffect} from 'react';
import {Route, Redirect} from 'react-router-dom';

import Loading from '../Partials/Loading';
import auth from '../../auth';

export default({
    component: Component,
    ...rest
}) => {

    const [isAuth,
        setIsAuth] = useState(null)

    useEffect(() => {
        const main = async() => {
            const authStatus = await auth.isAuthenticated();
            setIsAuth(authStatus);
        }
        main()
    }, [])

    if (isAuth === null) {
        return <Route component={Loading}/>
    } else {
        return (
            <Route
                {...rest}
                render={(props) => (isAuth
                ? <Component {...props} />
                : <Redirect
                    to={{
                    pathname: "/login",
                    state: {
                        from: props.location
                    }
                }}/>)}/>
        )
    }

}
