import React from 'react';
import ReactHtmlParser from 'react-html-parser';

import Editor from '../../Partials/RichTextEditor';

export default({
    activeTabId,
    course,
    courseDescription,
    handleEditorChange,
    handleEditorCancel,
    handleEditorSave
}) => (
    <div
        id="description-view"
        className={`description tab-view ${activeTabId === "description" && "is-visible"}`}>
        <div className="buttons">
            <button
                className="button is-success"
                disabled={courseDescription === course.description}
                onClick={handleEditorSave}>Save</button>
            <button
                className="button is-danger"
                disabled={courseDescription === course.description}
                onClick={handleEditorCancel}>Cancel</button>
        </div>
        <div className="is-flex">
            <div className="editor-container">
                <Editor handleEditorChange={handleEditorChange} value={courseDescription}/>
            </div>
            <div className="content">
                <h1 className="is-size-4">Preview Description</h1>
                {ReactHtmlParser(courseDescription)}
            </div>
        </div>
    </div>
)