//Import dependencies
import React, {Fragment, useState, useEffect} from 'react';
import {connect} from 'react-redux';
import axios from 'axios';
import 'bulma';
import './index.scss';

//Import components
import Navbar from '../Navbar';

//Import utils
import util from '../../util';

//Map State to Props
const mapStateToProps = (state) => {
    return {}
}

//Build container
const Courses = (props) => {
    const [courses,
        setCourses] = useState([]);
    const [filteredCourses,
        setFilteredCourses] = useState([]);
    const [teachers,
        setTeachers] = useState([]);
    const [searchTitle,
        setSearchTitle] = useState('');
    const [searchTeacher,
        setSearchTeacher] = useState('');
    const [modalOpen,
        setModalOpen] = useState(false);

    useEffect(() => {
        const main = async() => {
            try {
                let res = await axios.get('api/course/all');
                setCourses(res.data.payload);
                setFilteredCourses(res.data.payload);
            } catch (err) {
                err.response ? util.notify(props.dispatch, err.response.data.msg, "is-danger") : console.log(err);
            }
        }
        main();
    }, [props])

    const onModalToggle = async (e) => {
        e.preventDefault();
        setModalOpen(!modalOpen);
        if (e.target.name !== "closeModal" ) {
            let response = await axios.get('api/teacher/all');
            let data = response.data.payload;
            setTeachers(data);
        }
    }

    const onSearchTitleChange = (e) => {
        e.preventDefault();
        setSearchTitle(e.target.value);
    }

    const onSearchTeacherChange = (e) => {
        e.preventDefault();
        setSearchTeacher(e.target.value);
    }

    const handleSubmit = async(event) => {
        event.preventDefault();
        let form = document.getElementById('createCourse');
        let payload = new FormData(form);
        onModalToggle(event);

        try {
            const res = await axios.post("api/course", payload);
            setCourses(res.data.payload);
            setFilteredCourses(res.data.payload);
            util.notify(props.dispatch, "Course Created", "is-success");
        } catch (err) {
            err.response ? util.notify(props.dispatch, err.response.data.msg, "is-danger") : console.log(err);
        }
    }

    const onClickSearch = (e) => {
        e.preventDefault();
        const searchedCourses = courses.filter(course => {
            let teacherName = '';
            course.teacher.name.last
                ? (teacherName = course.teacher.name.first + ' ' + course.teacher.name.last)
                : (teacherName = course.teacher.name.first);
            let courseTitle = course.title;
            if ((teacherName.toLowerCase().indexOf(searchTeacher.toLowerCase()) !== -1) && (courseTitle.toLowerCase().indexOf(searchTitle.toLowerCase()) !== -1)) {
                return true;
            } else {
                return false;
            }
        })
        setFilteredCourses(searchedCourses);
    }

    const onClickResetSearch = async(e) => {
        e.preventDefault();
        setSearchTitle('');
        setSearchTeacher('');
        setFilteredCourses(courses);
    }

    return (
        <Fragment>
            <Navbar/>
            <section className="courses">
                <div className="container">
                    <h1 className="is-size-3">Courses</h1>
                    <div className="actions is-flex">
                        <div className="search is-flex">
                            <input
                                className="input search-box"
                                type="text"
                                placeholder="Search by course title"
                                onChange={onSearchTitleChange}
                                value={searchTitle}/>
                            <input
                                className="input search-box"
                                type="text"
                                placeholder="Search by teacher"
                                onChange={onSearchTeacherChange}
                                value={searchTeacher}/>
                            <button className="button is-primary" onClick={onClickSearch}>Search</button>
                            <button className="button is-info" onClick={onClickResetSearch}>Reset</button>
                        </div>
                        <div className="add is-flex" onClick={onModalToggle}>
                            <p className="is-size-5" >Add Course</p>
                            <img
                                src={process.env.PUBLIC_URL + '/images/add.png'}
                                alt="add"
                                className="icon"/>
                        </div>
                    </div>
                    <div className={`modal ${modalOpen && 'is-active'}`}>
                        <div className="modal-background"></div>
                        <div className="modal-card">
                            <header className="modal-card-head">
                                <p className="modal-card-title">New Course</p>
                                <button className="delete" aria-label="close" onClick={onModalToggle}></button>
                            </header>
                            <section className="modal-card-body">
                                <form
                                    id="createCourse"
                                    onSubmit={handleSubmit}
                                    className="form"
                                    encType="multipart/form-data">
                                    <input
                                        type="text"
                                        className="input item"
                                        name="title"
                                        placeholder="Unique Title"
                                        required/>
                                    <input
                                        type='text'
                                        placeholder="Teacher Id"
                                        name="teacherid"
                                        className='input item'
                                        autoComplete='off'
                                        list='teachers'
                                        required/>
                                    <datalist id="teachers">
                                        {teachers.map(teacher => <option value={teacher._id} key={teacher._id}>
                                            {teacher.name.last
                                                ? (teacher.name.first + " " + teacher.name.last)
                                                : (teacher.name.first)}
                                        </option>)}
                                    </datalist>
                                </form>
                            </section>
                            <footer className="modal-card-foot">
                                <button form="createCourse" type="submit" className="button is-success" name="submitCourse">Add</button>
                                <button className="button" onClick={onModalToggle} name="closeModal">Cancel</button>
                            </footer>
                        </div>
                    </div>
                    <table className="table is-bordered is-hoverable is-fullwidth">
                        <thead>
                            <tr>
                                <th>Title</th>
                                <th>Teacher</th>
                                <th>Status</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {filteredCourses.map(course => {
                                let teacherName = '';
                                course.teacher.name.last
                                    ? (teacherName = course.teacher.name.first + ' ' + course.teacher.name.last)
                                    : (teacherName = course.teacher.name.first);
                                return (
                                    <tr key={course._id} className={course.status}>
                                        <td>{course.title}</td>
                                        <td>{teacherName}</td>
                                        <td>{course.status}</td>
                                        <td>
                                            <a href={`/courses/${course._id}/edit`}>
                                                <button className="button is-small is-info">Edit</button>
                                            </a>
                                            <a href={`/courses/${course.titleId}/view/preview`}>
                                                <button className="button is-small is-primary">View</button>
                                            </a>
                                        </td>
                                    </tr>
                                )
                            })}
                        </tbody>
                    </table>
                </div>
            </section>
        </Fragment>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(Courses);