//Import dependencies
import React from 'react';
import {connect} from 'react-redux';
import 'bulma';

//Import components

//Import actions & auth
import actions from '../../state/actions';
import auth from '../../auth';

const mapStateToProps = (state) => {
    return {}
}

//Build container
const Navbar = (props) => {
    const handleLogOut = async(event) => {
        event.preventDefault();
        auth.logout(props.dispatch, actions.logOut);
    }
    return (
        <nav className="navbar is-warning" role="navigation" aria-label="main navigation">
            <div className="container">
                <div className="navbar-start">
                    <a className="navbar-item" href='/'>
                        Home
                    </a>
                    <a className="navbar-item" href='/courses'>
                        Courses
                    </a>
                    <a className="navbar-item" href='/teachers'>
                        Teachers
                    </a>
                    <a className="navbar-item" href='/users'>
                        Users
                    </a>
                </div>
                <div className="navbar-end">
                    <div className="navbar-item">
                        <p onClick={handleLogOut} style={{"cursor" : "pointer", "fontWeight" : "bold"}}>Log Out</p>
                    </div>
                </div>
            </div>
        </nav>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(Navbar);