import React, {useState} from 'react';

export default({
    activeTabId,
    courseSections,
    newSectionName,
    setNewSectionName,
    handleNewSectionAdd,
    handleNewSectionCancel,
    handleSectionDelete,
    activeSectionId,
    setActiveSectionId,
    courseLessons,
    handleNewLessonAdd,
    addLessonOpen,
    setAddLessonOpen,
    handleLessonView,
    handleLessonDelete,
    uploadStatus,
    setUploadStatus
}) => {
    const [fileName,
        setFileName] = useState('');
    return (
        <div
            id="sections-view"
            className={`sections tab-view ${activeTabId === "sections" && "is-visible"}`}>
            <div className="is-flex">
                <aside className="menu">
                    <input
                        type="text"
                        className="input"
                        value={newSectionName}
                        onChange={(e) => {
                        e.preventDefault();
                        setNewSectionName(e.target.value)
                    }}/>
                    <button className="button is-small is-success" onClick={handleNewSectionAdd}>Add</button>
                    <button className="button is-small is-danger" onClick={handleNewSectionCancel}>Cancel</button>
                    <p className="menu-label">
                        Sections
                    </p>
                    <ul className="menu-list">
                        {courseSections.map((sec, index) => <li
                            key={sec.id}
                            onClick={(e) => {
                            e.preventDefault();
                            setActiveSectionId(sec.id);
                        }}>
                            <a href="#sections" className={`${activeSectionId === sec.id && "is-active"}`}>{`${index + 1}. ${sec.title}`}</a>
                        </li>)}
                    </ul>
                </aside>
                <div className="active-section">
                    <div className="is-flex actions">
                        <h1 className="is-size-4 heading">{`Section: ${activeSectionId}`}</h1>
                        <button className="button is-danger" onClick={handleSectionDelete}>Delete Section</button>
                    </div>
                    <div className="is-flex actions">
                        <h1 className="is-size-4">Lessons</h1>
                        <div
                            className="add is-flex"
                            onClick={(e) => {
                            e.preventDefault();
                            setAddLessonOpen(true);
                            setUploadStatus(0);
                        }}>
                            <p className="is-size-5">Add Lesson</p>
                            <img
                                src={process.env.PUBLIC_URL + '/images/add.png'}
                                alt="add"
                                className="icon"/>
                        </div>
                    </div>
                    <form
                        id="addNewLesson"
                        className={`${addLessonOpen && "is-visible"}`}
                        encType="multipart/form-data"
                        onSubmit={(e) => {
                        e.preventDefault();
                        handleNewLessonAdd(e, setFileName);
                    }}>
                        <div className="field is-horizontal">
                            <label className="label">Lesson Type</label>
                            <div className="control">
                                <select className="select item" name="lessontype" required>
                                    <option value="">Select...</option>
                                    <option value="VIDEO">Video</option>
                                    <option value="DOC">Document</option>
                                </select>
                            </div>
                            <label className="label">Lesson Access</label>
                            <div className="control">
                                <select className="select item" name="lessonaccess" required>
                                    <option value="">Select...</option>
                                    <option value="Preview">Preview</option>
                                    <option value="Freeview">Freeview</option>
                                    <option value="Premium">Premium</option>
                                </select>
                            </div>
                        </div>
                        <div className="field">
                            <label className="label">Lesson Title</label>
                            <div className="control">
                                <input
                                    className="input item"
                                    name="title"
                                    type="text"
                                    required
                                    autoComplete="off"/>
                            </div>
                        </div>
                        <div className="file has-name is-fullwidth">
                            <label className="file-label">
                                <input
                                    className="file-input"
                                    type="file"
                                    name="resource"
                                    required
                                    onChange={(e) => {
                                    setFileName(e.target.files[0].name)
                                }}/>
                                <span className="file-cta">
                                    <span className="file-label">
                                        Upload File
                                    </span>
                                </span>
                                <span className="file-name">{fileName}</span>
                            </label>
                        </div>
                        {uploadStatus > 0 ? <p>{`${uploadStatus}% Uploaded`}</p> : null}
                        <button type="submit" className="button is-success is-small">Add</button>
                        <button
                            className="button is-danger is-small"
                            onClick={(e) => {
                            e.preventDefault();
                            setAddLessonOpen(false);
                            document
                                .getElementById('addNewLesson')
                                .reset()
                        }}>Cancel</button>
                    </form>
                    <table className="table is-bordered is-hoverable is-fullwidth">
                        <thead>
                            <tr>
                                <th>Title</th>
                                <th>Content Type</th>
                                <th>Access Type</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {courseLessons
                                .filter(lesson => lesson.sectionid === activeSectionId)
                                .map((lesson, index) => {
                                    return (
                                        <tr key={lesson.lesson._id}>
                                            <td>
                                                {lesson.lesson.title}
                                            </td>
                                            <td>
                                                {lesson.lesson.lessontype === "VIDEO"
                                                    ? "Video"
                                                    : (lesson.lesson.lessontype === "DOC"
                                                        ? "Document"
                                                        : "")}
                                            </td>
                                            <td>
                                                {lesson.access}
                                            </td>
                                            <td>
                                                <button
                                                    className="button is-primary is-small"
                                                    value={lesson.lesson._id}
                                                    onClick={handleLessonView}>View</button>
                                                <button
                                                    className="button is-danger is-small"
                                                    value={lesson.lesson._id}
                                                    onClick={(e) => {handleLessonDelete(e, lesson.lesson._id)}}>Delete</button>
                                            </td>
                                        </tr>
                                    )
                                })
}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}