import React from 'react';
import ReactPlayer from 'react-player';

export default({lesson, lessonUrl}) => {
    if (lesson.lesson === "no-access") {
        return (
            <div>No Access</div>
        )
    } else if (lesson.lessontype === "VIDEO") {
        return (
            <div
                onContextMenu={(e) => {
                e.preventDefault();
                return false;
            }}>
                <ReactPlayer
                    width="900px"
                    height="510px"
                    url={lessonUrl}
                    controls={true}
                    config={{
                    file: {
                        attributes: {
                            controlsList: 'nodownload'
                        }
                    }
                }}/>
            </div>
        )
    } else if (lesson.lessontype === "DOC") {
        return (<iframe title="document" width="900px" height="510px" src={lessonUrl}/>)
    } else {
        return <div>Loading</div>
    }
}