//Import dependencies
import React, {Fragment, useEffect, useState} from 'react';
import {connect} from 'react-redux';
import axios from 'axios';
import 'bulma';
import './index.scss';
//Import components
import Navbar from '../Navbar';

//Import utils
import util from '../../util';

//Map State to Props
const mapStateToProps = (state) => {
    return {permission: state.logInReducer.permission}
}

//Build container
const TemplateContainer = (props) => {

    const [users,
        setUsers] = useState([]);
    const [filteredUsers,
        setFilteredUsers] = useState([]);
    const [searchName,
        setSearchName] = useState('');
    const [searchEmail,
        setSearchEmail] = useState('');
    const [modalOpen,
        setModalOpen] = useState(false);

    useEffect(() => {
        const main = async() => {
            try {
                let response = await axios.get('api/user/all');
                let data = response.data.payload;
                setUsers(data);
                setFilteredUsers(data);
            } catch (err) {
                util.notify(props.dispatch, err.response.data.msg, "is-danger");
            }
        }
        main();
    }, [props])

    const onModalToggle = (e) => {
        e.preventDefault();
        setModalOpen(!modalOpen);
    }

    const onSearchNameChange = (e) => {
        e.preventDefault();
        setSearchName(e.target.value);
    }

    const onSearchEmailChange = (e) => {
        e.preventDefault();
        setSearchEmail(e.target.value);
    }

    const handleSubmit = async(event) => {
        event.preventDefault();
        let form = document.getElementById('createUser');
        let payload = new FormData(form);
        onModalToggle(event);

        try {
            const res = await axios.post("api/user", payload);
            setUsers(res.data.payload);
            setFilteredUsers(res.data.payload);
            util.notify(props.dispatch, "User Created", "is-success");
        } catch (err) {
            err.response ? util.notify(props.dispatch, err.response.data.msg, "is-danger") : console.log(err);
        }
    }

    const onClickSearch = (e) => {
        e.preventDefault();
        const searchedUsers = users.filter(user => {
            let userName = '';
            user.name.last
                ? (userName = user.name.first + ' ' + user.name.last)
                : (userName = user.name.first);
            let userEmail = user.email;
            if ((userName.toLowerCase().indexOf(searchName.toLowerCase()) !== -1) && (userEmail.toLowerCase().indexOf(searchEmail.toLowerCase()) !== -1)) {
                return true;
            } else {
                return false;
            }
        })
        setFilteredUsers(searchedUsers);
    }

    const onClickResetSearch = async(e) => {
        e.preventDefault();
        setSearchName('');
        setSearchEmail('');
        setFilteredUsers(users);
    }

    const onClickDelete = async(e) => {
        e.preventDefault();
        const id = e.target.value;
        try {
            const res = await axios.delete(`/api/user/${id}`);
            const data = res.data;
            setUsers(data.payload);
            setFilteredUsers(data.payload);
            util.notify(props.dispatch, "User Deleted", "is-success");
        } catch (err) {
            err.response ? util.notify(props.dispatch, err.response.data.msg, "is-danger") : console.log(err);
        }
    }

    const onClickReset = async(e) => {
        e.preventDefault();
        try {
            await axios.put('/api/user/password/reset', {id: e.target.value})
            util.notify(props.dispatch, "Password Reset Successfully", "is-success");
        } catch (err) {
            err.response ? util.notify(props.dispatch, err.response.data.msg, "is-danger") : console.log(err);
        }
    }

    return (
        <Fragment>
            <Navbar/>
            <section className="users">
                <div className="container">
                    <h1 className="is-size-3">Users</h1>
                    <div className="actions is-flex">
                        <div className="search is-flex">
                            <input
                                className="input search-box"
                                type="text"
                                placeholder="Search by name"
                                onChange={onSearchNameChange}
                                value={searchName}/>
                            <input
                                className="input search-box"
                                type="text"
                                placeholder="Search by email"
                                onChange={onSearchEmailChange}
                                value={searchEmail}/>
                            <button className="button is-primary" onClick={onClickSearch}>Search</button>
                            <button className="button is-info" onClick={onClickResetSearch}>Reset</button>
                        </div>
                        <div className="add is-flex" onClick={onModalToggle}>
                            <p className="is-size-5">Add User</p>
                            <img
                                src={process.env.PUBLIC_URL + '/images/add.png'}
                                alt="add"
                                className="icon"/>
                        </div>
                    </div>
                    <div className={`modal ${modalOpen && 'is-active'}`}>
                        <div className="modal-background"></div>
                        <div className="modal-card">
                            <header className="modal-card-head">
                                <p className="modal-card-title">New User</p>
                                <button className="delete" aria-label="close" onClick={onModalToggle}></button>
                            </header>
                            <section className="modal-card-body">
                                <form
                                    id="createUser"
                                    onSubmit={handleSubmit}
                                    className="form"
                                    encType="multipart/form-data">
                                    <div className="field is-horizontal">
                                        <input
                                            type="text"
                                            className="input first"
                                            name="firstname"
                                            placeholder="First Name"
                                            required/>
                                        <input
                                            type="text"
                                            className="input"
                                            name="lastname"
                                            placeholder="Last Name"
                                            required/>
                                    </div>
                                    <input
                                        type="email"
                                        className="input item"
                                        name="email"
                                        placeholder="Email Id"
                                        required/>
                                    <select name="permissionlevel" className="select item" required>
                                        <option value=''>Permission</option>
                                        <option value={3}>EDITOR</option>
                                        {props.permission <= 2
                                            ? <option value={2}>MODERATOR</option>
                                            : null}
                                        {props.permission <= 1
                                            ? <option value={1}>ADMIN</option>
                                            : null}
                                        {props.permission === 0
                                            ? <option value={0}>SUPERUSER</option>
                                            : null}
                                    </select>
                                </form>
                            </section>
                            <footer className="modal-card-foot">
                                <button form="createUser" type="submit" className="button is-success">Add</button>
                                <button className="button" onClick={onModalToggle}>Cancel</button>
                            </footer>
                        </div>
                    </div>
                    <table className="table is-bordered is-hoverable is-fullwidth">
                        <thead>
                            <tr>
                                <th>Name</th>
                                <th>Email Id</th>
                                <th>Permission</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {filteredUsers.map(user => {
                                let userName = '';
                                user.name.last
                                    ? (userName = user.name.first + ' ' + user.name.last)
                                    : (userName = user.name.first);
                                return (
                                    <tr key={user._id}>
                                        <td>{userName}</td>
                                        <td>{user.email}</td>
                                        <td>{user.permission.title}</td>
                                        <td>
                                            <button
                                                className="button is-small is-danger"
                                                value={user._id}
                                                onClick={onClickDelete}>Delete User</button>
                                            <button
                                                className="button is-small is-info"
                                                value={user._id}
                                                onClick={onClickReset}>Reset Password</button>
                                        </td>
                                    </tr>
                                )
                            })}
                        </tbody>
                    </table>
                </div>
            </section>
        </Fragment>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(TemplateContainer);